<template>
  <div class="list">
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">渠道基本信息：</h4>
      </div>
      <div class="pull-right">
        <el-button   @click="">删除</el-button>
        <el-button   @click="">保存</el-button>
      </div>
    </div>
    <div class="clearfix list-center">
      <!--<h-table></h-table>-->
      <el-col :span="12">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px" class="demo-ruleForm">
          <el-form-item label="渠道编号" >
            <el-input v-model="ruleForm.id" readonly v-if="ruleForm.editorId"></el-input>
            <p class="list-editor"  v-else >{{ruleForm.id}}</p>
          </el-form-item>
          <el-form-item label="渠道名称" prop="name">
            <el-input v-model="ruleForm.name"
                      @blur="editorForm(ruleForm.editorName,'editorName')"
                      v-focus="ruleForm.editorName"
                      v-if="ruleForm.editorName">
            </el-input>
            <p class="list-editor" @click="editorForm(ruleForm.editorName,'editorName')" v-else>
              {{ruleForm.name}}
            </p>
          </el-form-item>
          <el-form-item label="渠道联系人" prop="contact">
            <el-input v-model="ruleForm.contact"
                      @blur="editorForm(ruleForm.editorContact,'editorContact')"
                      v-focus="ruleForm.editorContact"
                      v-if="ruleForm.editorContact">
            </el-input>
            <p class="list-editor" @click="editorForm(ruleForm.editorContact,'editorContact')" v-else>
              {{ruleForm.contact}}
            </p>
          </el-form-item>
          <el-form-item label="渠道联系人手机号" prop="phone">
            <el-input v-model="ruleForm.phone"
                      @blur="editorForm(ruleForm.editorPhone,'editorPhone')"
                      v-focus="ruleForm.editorPhone"
                      v-if="ruleForm.editorPhone">
            </el-input>
            <p class="list-editor" @click="editorForm(ruleForm.editorPhone,'editorPhone')" v-else>
              {{ruleForm.phone}}
            </p>
          </el-form-item>
        </el-form>
      </el-col>
    </div>
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">备注：</h4>
      </div>
    </div>
    <div class="list-center-bottom" style="height: 140px;border: 1px solid #dfdfdf;background: #fafafa">
      <el-input type="textarea" :rows="8"
                placeholder="请输入内容"
                @blur="editorForm(editorRemark,'editorRemark')"
                v-focus="editorRemark"
                v-model="textarea" v-if="editorRemark"></el-input>
      <div class="list-remarks" @click="editorForm(editorRemark,'editorRemark')" v-else>
        {{textarea}}
      </div>
    </div>
  </div>
</template>
<script>
  import HTable from '@/components/layout/table/HTable'
  export default {
    name: 'chanceDetail',
    components: {
      HTable
    },
    data () {
      return {
        ruleForm: {
          index: '',
          editorId: true,
          name: '',
          editorName: true,
          contact: '',
          editorContact: true,
          phone: '',
          editorPhone: true
        },
        rules: {
          name: [
            { required: true, message: '请输入渠道名称', trigger: 'blur' }
          ],
          contact: [
            { required: true, message: '请输入渠道联系人', trigger: 'blur' }
          ],
          phone: [
            { required: true, message: '请输入渠道联系人手机号', trigger: 'blur' }
          ]
        },
        type: 'create',
        textarea: '',
        editorRemark: true
      }
    },
    watch: {
      /**
       * @params 监听参数变化重新获取数据
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      editorForm (value, obj) {
        if (this.type === 'detail') {
          if (obj === 'editorRemark') {
            this.editorRemark = !value
          } else {
            this.ruleForm[obj] = !value
            if (event.type === 'blur') {
              if (obj === 'editorName') {
                if (this.ruleForm.name === '') {
                  this.ruleForm[obj] = value
                }
              }
              if (obj === 'editorContact') {
                if (this.ruleForm.contact === '') {
                  this.ruleForm[obj] = value
                }
              }
              if (obj === 'editorPhone') {
                if (this.ruleForm.phone === '') {
                  this.ruleForm[obj] = value
                }
              }
            }
          }
        }
      },
      /**
       * @params:category 分类 page：页码 limit:条数
       * */
      getData (params) {
      },
      /**
       * @on-change 页码改变的回调，返回改变后的页码
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * @on-page-size-change 切换每页条数时的回调，返回切换后的每页条数
       * */
      PageSizeChange (limit) {
        this.params.limit = limit
      },
      /**
       * 表格对应操作方法
       * @show 查看
       * @remove 删除
       * @edit
       */
      remove (index) {
        this.listData.splice(index, 1)
      },
      edit (index) {
      },
      /**
       * 多选
       * selection：已选项数据 row：刚选择的项数据
       */
      onSelect (selection, row) {
        // console.log(selection,row)
      },
      onSelectionChange (selection) {
        this.selection = selection
      }
    },
    created () {
      this.getData(this.params)
      if (this.$route.query.type === 'detail') {
        this.type = 'detail'
        this.ruleForm = {
          id: '1',
          editorId: false,
          name: '李四',
          editorName: false,
          contact: '橙悦',
          editorContact: false,
          phone: '1331313131313',
          editorPhone: false
        }
        this.editorRemark = false
      }
    },
    mounted () {
    }
  }
</script>



